<template>
    <view class="goods"
	@click="$kt.util.navTo('/pages/shop/publich/goods-body?id='+item.id)"
	
	>
        <view
        v-if="JSON.parse(item.bodyFileListStr) && JSON.parse(item.bodyFileListStr).length > 0">
            <image
            v-if="JSON.parse(item.bodyFileListStr)[0].fileType == 'image'"
             :src="$kt.file.visit(JSON.parse(item.bodyFileListStr)[0].fileId)" style="
                    border-radius: 10rpx 10rpx 0 0;
                    width: 100%;
                    " mode="widthFix" />
        </view>
        <view class="goods-info">
            <text class="goods-title">
                {{item.title}}
                <text
                v-if="item.subTitle"
                >
                {{" "}}{{ item.subTitle }}
                </text>
            </text>
            <view style="height: 10rpx;"></view>
        </view>
        <view class="goods-price">
                {{item.charmPoint}}碎片
            </view>
    </view>
</template>

<script>
export default {
    props: {
        item: {
            type: Object,
            default: () => {
                return {}
            }
        }
    },
    data() {
        return {}
    },
    methods: {}
}
</script>

<style lang="scss" scoped>
.goods {
    display: inline-block;
    width: calc(50% - 40rpx);
    box-sizing: border-box;
    border-radius: 10rpx;
    margin: 10rpx 20rpx;
    vertical-align: top;
    background-color: #fff;
}

.goods:active {
    transform: scale(0.98);
}

.goods-info {
    padding: 10rpx 20rpx 20rpx 20rpx;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 0 0 10rpx 10rpx;
    color: #333;
    height: 90rpx;
}

.goods-title {
    font-size: 30rpx;
}

.goods-price {
    font-size: 26rpx;
    color: #666;
    text-align: right;
    padding: 10rpx;
}

</style>